<template>
  <el-col :span="24" class="main">
    <div>
      <div id="chartLineBox" style="width: 100%;height: 400px;"></div>
    </div>
  </el-col>
</template>
<script>
//
import echarts from "echarts";
export default {
  props: {
    lineData: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      option: {}
    };
  },

  //echarts
  mounted: function() {
    this.chartLine = echarts.init(document.getElementById("chartLineBox"));

    // 
    this.option = {
      tooltip: {
        //tip
        trigger: "axis"
      },

      legend: {
        //
        data: ["", ""]
      },
      color: ["#8AE09F", "#FA6F53"], // legend 
      xAxis: {
        //x
        type: "category",
        boundaryGap: false, //
        data: this.lineData.timeData,
        // data: [
        //   "2019-06",
        //   "2019-07",
        //   "2019-08",
        //   "2019-09",
        //   "2019-10",
        //   "2019-11",
        //   "2019-12",
        //   "2020-01",
        //   "2020-02",
        //   "2020-03",
        //   "2020-04",
        //   "2020-05"
        // ],
        name: "", //X name
        nameTextStyle: {
          //
          color: "rgb(39, 39, 39)",
          fontSize: 16,
          padding: [0, 0, 0, 20]
        },
        axisLine: {
          //
          lineStyle: {
            color: "rgb(39, 39, 39)"
          }
        }
      },
      yAxis: {
        name: "",
        nameTextStyle: {
          color: "rgb(39, 39, 39)",
          fontSize: 16,
          padding: [0, 0, 10, 0]
        },
        axisLine: {
          lineStyle: {
            color: "rgb(39, 39, 39)"
          }
        },
        type: "value"
      },
      series: [
        {
          name: "",
          data: this.lineData.allData,
          // data: [100, 120, 130, 150, 200, 230, 250, 750, 730],
          type: "line", // 
          lineStyle: {
            //  => normal
            normal: {
              color: "#8AE09F"
            }
          }
        },
        {
          name: "",
          data: this.lineData.classData,
          // data: [12, 20, 21, 32, 35, 39, 40, 44, 44],
          type: "line",
          lineStyle: {
            normal: {
              color: "#FA6F53"
            }
          }
        }
      ]
    };

    // 
    this.chartLine.setOption(this.option, true);
  },
  watch: {
    "lineData.classData": function() {
      //  console.log(this.lineData.classData)

      //  this.lineData.classData = this.lineData.classData
      this.option.series[1].data = this.lineData.classData;
      this.chartLine.setOption(this.option, true);
      //  console.log(this.option)
    }
  },

  created() {}
};
</script>
<style scoped>
.wrapper {
  width: 7rem;
  height: 5rem;
  color: rgb(39, 39, 39);
}
</style>